<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)"/>
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号自动注册</p>
      </div>
      <div class="from">
        <div class="from-item">
          <input v-model="Phone" type="text" class="inp" maxlength="11" placeholder="请输入手机号">
        </div>
        <div class="from-item">
          <input v-model="PicCode" type="text" class="inp" maxlength="4" placeholder="输入条形验证码">
          <img v-if="PicUrl" :src="PicUrl" alt="" @click="GetPicCode()">
        </div>
          <div class="from-item">
          <input v-model="SmsCode" type="text" class="inp" placeholder="请输入短信验证码"  maxlength="6">
          <button @click="getCode">{{Second===TotalSecond?'获取验证码':Second+'秒后重新发送'}}</button>
        </div>
      </div>
      <div @click="login" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
// 导入axios配置的request
import { getPicCode, getMsgCode, codelogin } from '@/api/login'
// 导入vant轻提示、弹窗组件
import { Toast, Dialog } from 'vant'
// 导入vuex
import { mapMutations } from 'vuex'
export default {
  name: 'LoginIndex',
  async created () {
    if (this.$store.getters.token) {
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
    this.GetPicCode()
  },
  data () {
    return {
      // 图片验证码的key
      PicKey: '',
      // 图片地址
      PicUrl: '',
      // 倒计时总数
      TotalSecond: 60,
      // 倒计时秒数
      Second: 60,
      // 定时器
      timeout: null,
      // 手机号
      Phone: '',
      // 图片验证码
      PicCode: '',
      // 短信验证码
      SmsCode: ''
    }
  },
  methods: {
    ...mapMutations('user', ['setUserInfo']),
    async GetPicCode () {
    //  获取验证码值
      const { data: { base64, key } } = await getPicCode()
      this.PicUrl = base64
      this.PicKey = key
    },
    // 校验手机号和验证码是否合法
    vaild () {
      if (!/^1[0-9]\d{9}$/.test(this.Phone)) {
        Toast('请输入正确手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.PicCode)) {
        Toast('请输入正确验证码')
        return false
      }
      return true
    },
    // 倒计时
    async getCode () {
      // 校验手机号和验证码是否合法
      if (!this.vaild()) {
        return
      }
      // 发送验证码
      const res = await getMsgCode(this.PicCode, this.PicKey, this.Phone)
      Toast('验证码发送成功')
      // vant弹窗
      Dialog({
        message: res.message,
        confirmButtonColor: '#cea26a'
      }).then(() => {
      })
      // 倒计时
      if (!this.timeout && this.Second === this.TotalSecond) {
        this.timeout = setInterval(() => {
          this.Second--
          if (this.Second <= 0) {
            clearInterval(this.timeout)
            this.timeout = null
            this.Second = this.TotalSecond
          }
        }, 1000)
      }
    },
    // 登录
    async login () {
      if (!this.vaild()) {
        return
      }
      if (!/^\d{6}$/.test(this.SmsCode)) {
        return Toast('请输入正确验证码')
      }
      const res = await codelogin(this.Phone, this.SmsCode)
      Toast('登录成功')
      // 存入vuex
      this.setUserInfo(res.data)
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },
  // 离开页面清除定时器（节省性能）
  destroyed () {
    clearInterval(this.timeout)
  }
}
</script>

<style lang="less" scoped>
  .container{
    padding: 49px, 29px;
    .title{
      margin-bottom: 20px;
      h3{
        font-size: 26px;
        font-weight: normal;
      }
      p{
        line-height: 40px;
        font-size: 14px;
        color: #b8b8b8;
      }
    }
    .from-item{
      border-bottom: 1px solid #f3f1f2;
      padding: 13px;
      margin-bottom: 14px;
      display: flex;
      align-items: center;
      .inp{
        display: block;
        border: none;
        outline: none;
        height: 32px;
        font-size: 14px;
        flex: 1;
      }
      img{
        width: 94px;
        height: 31px;
      }
      button{
        height: 31px;
        border: none;
        font-size: 13px;
        color: #cea26a;
        background-color: transparent;
        padding-right: 9px;
      }
    }
   .login-btn{
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    }
   }
</style>
